<template>
    <nav-bar></nav-bar>
    <div class="w-full h-[70vh] flex justify-center items-center overflow-hidden">

        <div
            class="flex flex-col w-full max-w-md px-4 py-8 bg-white rounded-lg shadow dark:bg-gray-800 sm:px-6 md:px-8 lg:px-10">
            <div class="self-center mb-6 text-xl font-light text-gray-600 sm:text-2xl dark:text-white">
                Digite o codigo enviado por email aqui:
            </div>
            <div class="mt-8">
                <form action="#" autoComplete="off">
                    <div class="flex flex-col mb-2">
                        <div class="flex relative ">
                            <span
                                class="rounded-l-md inline-flex  items-center px-3 border-t bg-white border-l border-b  border-gray-300 text-gray-500 shadow-sm text-sm">
                                <svg width="15" height="15" fill="currentColor" viewBox="0 0 1792 1792"
                                    xmlns="http://www.w3.org/2000/svg">
                                    <path
                                        d="M1792 710v794q0 66-47 113t-113 47h-1472q-66 0-113-47t-47-113v-794q44 49 101 87 362 246 497 345 57 42 92.5 65.5t94.5 48 110 24.5h2q51 0 110-24.5t94.5-48 92.5-65.5q170-123 498-345 57-39 100-87zm0-294q0 79-49 151t-122 123q-376 261-468 325-10 7-42.5 30.5t-54 38-52 32.5-57.5 27-50 9h-2q-23 0-50-9t-57.5-27-52-32.5-54-38-42.5-30.5q-91-64-262-182.5t-205-142.5q-62-42-117-115.5t-55-136.5q0-78 41.5-130t118.5-52h1472q65 0 112.5 47t47.5 113z">
                                    </path>
                                </svg>
                            </span>
                            <input v-model="code" type="number" id="sign-in-email"
                                class=" rounded-r-lg flex-1 appearance-none border border-gray-300 w-full py-2 px-4 bg-white text-gray-700 placeholder-gray-400 shadow-sm text-base focus:outline-none focus:ring-2 focus:ring-purple-600 focus:border-transparent"
                                placeholder="Seu codigo" />
                        </div>
                    </div>


                    <div class="flex w-full" ref="formContainer">
                        <button @click.prevent="enviarCodigo" type="submit"
                            class="py-2 px-4  bg-purple-600 hover:bg-purple-700 focus:ring-purple-500 focus:ring-offset-purple-200 text-white w-full transition ease-in duration-200 text-center text-base font-semibold shadow-md focus:outline-none focus:ring-2 focus:ring-offset-2  rounded-lg ">
                            Enviar
                        </button>
                    </div>
                </form>
            </div>
            <div class="flex items-center justify-center mt-6">
                <a href="#" target="_blank"
                    class="inline-flex items-center text-xs font-thin text-center text-gray-500 hover:text-gray-700 dark:text-gray-100 dark:hover:text-white">
                    <span class="ml-2">
                        <router-link to="/cadastro">Retornar ao formulario de cadastro</router-link>
                    </span>
                </a>
            </div>
        </div>


        <div class="absolute flex justify-center" :style="{ transform: `translateX(-${translate}px)` }" id="messageErr">
            <div
                class="flex flex-col gap-y-20 w-full px-4 py-4 mt-6 bg-white rounded-lg shadow-lg sm:w-1/2 md:w-1/2 min-h-[400px] sm:min-w-[500px]  dark:bg-gray-800">
                <div class="">
                    <div class="flex-shrink-0">
                        <div class="flex items-center justify-center w-12 h-12 mx-auto">
                            <img :src="img" alt="Error">
                        </div>
                    </div>
                    <h3 class="py-4 text-2xl font-semibold text-gray-700 sm:text-xl dark:text-white text-center">
                        {{ title }}
                    </h3>
                    <p class="py-4 text-gray-500 text-md dark:text-gray-300">
                        {{ response }}
                    </p>
                </div>


                <div>
                    <router-link to="/">

                        <button type="button"
                            class="py-2 px-4  bg-indigo-600 hover:bg-indigo-700 focus:ring-indigo-500 focus:ring-offset-indigo-200 text-white w-full transition ease-in duration-200 text-center text-base font-semibold shadow-md focus:outline-none focus:ring-2 focus:ring-offset-2  rounded-lg ">
                            Voltar
                        </button>

                    </router-link>
                </div>


            </div>
        </div>


    </div>
</template>


<script setup>
import { ref, onMounted } from 'vue';
import { useStore } from 'vuex'
import axios from 'axios'
import CardErr from '../components/Modules/cardERR'
import NavBar from '@/components/NavBar.vue';

const store = useStore()

const code = ref()

const data = ref({ code: null, User: store.state.UserCpf.User })

let translate = ref(10000)

let response = ref()

let title = ref()

let img = ref()


const formContainer = ref(null);




async function enviarCodigo() {
    console.log(data.value)
    data.value.code = code.value
    
    if (data.value.code === null | data.value.code == undefined) {
        //implementar sistema de cards

        console.log('Algo deu errado, preencha o campo obrigatorio')
    } else {
        await axios.post('http://192.168.0.181:3333/createCadastroVerify', data.value).then((res) => {



            response.value = res.data.message

            title.value = "Cadastro concluido"

            translate.value = 0

            img.value = "https://dettmann32.github.io/imagens-for-supershow-web-site/imagens/sucesso.png"




        }).catch((err) => {

            translate.value = CardErr.functionErro().open

            title.value = 'ERRO'
            response.value = 'Algo deu errado ao conectar-se com o servidor ! Tente novamente mais tarde'

            img.value = "https://dettmann32.github.io/imagens-for-supershow-web-site/imagens/ErroIcon.png"


        })

    }

}



onMounted(() => {
    formContainer.value.addEventListener('keyup', (event) => {
        if (event.key === 'Enter') {
            enviarCodigo();
        }
    });
});


</script>

<style> #messageErr {
     transition: 0.5s;
 }
</style>